<template lang="pug">
#work 
  .content-container
    .heading 
      .heading-row
        p Work #[br] Experience
        .heading-row-line
      .heading-column
        .heading-column-line
        .inner-content
          .flex-content
            .main-timeline-line
            .timeline
              .timeline-date(
                v-for="(timelineDate, index) in timelineDates",
                :key="'date-' + index"
              )
                .timeline-date-heading
                  .timeline-line
                  .date-container(
                    :class="{ 'selected-date-container': timelineDate === selectedDate }",
                    @click="selectDate(timelineDate)"
                  )
                    p {{ timelineDate }}
                .description(v-if="timelineDate === selectedDate")
                  .company-role
                    p {{ selectedTimeline.companyRole }}
                  .company-description
                    svg.bi.bi-caret-right-fill(
                      width="16px",
                      height="16px",
                      viewBox="0 0 16 16",
                      fill="currentColor",
                      xmlns="http://www.w3.org/2000/svg"
                    )
                      path(
                        d="M12.14 8.753l-5.482 4.796c-.646.566-1.658.106-1.658-.753V3.204a1 1 0 0 1 1.659-.753l5.48 4.796a1 1 0 0 1 0 1.506z"
                      )
                    p {{ selectedTimeline.companyDescription }}
                  .responsibilities
                    p.header Responsibilities:
                    .resp-item(
                      v-for="(resp, index) in selectedTimeline.workDescription",
                      :key="'resp-' + index"
                    )
                      svg.bi.bi-caret-right-fill(
                        width="16px",
                        height="16px",
                        viewBox="0 0 16 16",
                        fill="currentColor",
                        xmlns="http://www.w3.org/2000/svg"
                      )
                        path(
                          d="M12.14 8.753l-5.482 4.796c-.646.566-1.658.106-1.658-.753V3.204a1 1 0 0 1 1.659-.753l5.48 4.796a1 1 0 0 1 0 1.506z"
                        )
                      p {{ resp }}
</template>

<script>
export default {
  data() {
    return {
      selectedDate: "February 2020 - Present",
      timelines: [
        {
          date: "February 2020 - Present",
          companyRole: "Compass Interactive - Full Stack Developer",
          companyDescription: "Award winning digital agency",
          workDescription: [
            "Developed and maintained e-commerce/microsites for MNCs like Nike, IKEA, Kiehls, Brickmagic",
            "Involved in planning and estimation for project requirements",
            "Managed multiple projects at a time",
          ],
        },
        {
          date: "February 2019 - September 2019",
          companyRole: "Configura - Software Developer",
          companyDescription:
            "Swedish-based multinational company specialising in space-planning software with clients from the top companies in the industry globally.",
          workDescription: [
            "Worked in cross-regional teams to automate the build, test, release, and versioning process of the company's proprietary software.",
            "Optimised the total build time from hours to minutes",
            "Managed and supported the CI/CD ecosystem which included JIRA integration, automated ticket updates, automated release branch merges, and more.",
          ],
        },
        {
          date: "January 2018 - July 2018",
          companyRole: "Configura - Intern",
          companyDescription:
            "Swedish-based multinational company specialising in space-planning software with clients from the top companies in the industry globally.",
          workDescription: [
            "Implemented a high accuracy room detection system of floor plan images taken by low quality cameras",
            "Improved the quality and speed of the photo rendering system within the company's software.",
            "Added a preview feature which sped up the rendering from minutes to seconds.",
            "Added various other improvements and functionalities to the company's software.",
          ],
        },
      ],
    };
  },
  methods: {
    selectDate(date) {
      this.selectedDate = date;
    },
  },
  computed: {
    timelineDates() {
      return this.timelines.map((x) => x.date);
    },
    selectedTimeline() {
      return this.timelines.find((x) => x.date === this.selectedDate);
    },
  },
};
</script>

<style lang="sass" scoped>
#work
  width: 100%
  height: 100vh
  position: relative
  border: 1px solid red

.content-container
  position: absolute
  width: 100%
  max-width: 1024px
  transform: translate(-50%, -50%)
  top: 50%
  left: 50%

.flex-content
  display: flex
  width: 100%
  height: 100%
  max-height: 600px
  // overflow-y: scroll
  // overscroll-behavior: contain
  position: relative

.timeline
  position: relative
  margin-top: 20px
  text-align: left
  flex-shrink: 0
  overflow-y: scroll

.main-timeline-line
  position: absolute
  content: ''
  width: 4px
  background-color: #c7e4e9
  height: 100%
  top: 0%
  left: 0%

.timeline-date
  position: relative
  margin-left: 0px
  margin-bottom: 150px
  .timeline-date-heading
    display: flex
    align-items: center

    .timeline-line
      height: 2px
      width: 20px
      background-color: #c7e4e9

    .date-container
      border-radius: 40px
      background: transparent
      padding: 5px 10px
      border: 1px solid #c7e4e9
      cursor: pointer
      transition: all 0.25s ease
      p
        color: #c7e4e9

    .date-container:hover
      background-color: #c7e4e9
      p
        color: #2B7B8A
    .selected-date-container
      border-radius: 40px
      background-color: #c7e4e9
      padding: 5px 10px
      p
        color: #2B7B8A
.timeline-section
  position: relative
  width: 500px
  margin-left: 30px
  margin-bottom: 200px
  text-align: left
  p
    color: #c7e4e9

.description
  flex-grow: 0
  padding: 10px 40px
  text-align: left
  //animation: fadeIn 1s
  //transition: opacity 1s ease

@keyframes fadeIn
  0%
    opacity: 0
  100%
    opacity: 1

.company-description
  display: flex
  align-items: flex-start
  text-align: left
  svg
    flex-shrink: 0
    padding-right: 10px
    padding-top: 3px

.company-role
  text-align: left

.responsibilities
  margin-top: 40px

.resp-item
  margin-top: 10px
  display: flex
  align-items: flex-start
  text-align: left
  svg
    flex-shrink: 0
    padding-right: 10px
    padding-top: 3px

.inner-content
  padding: 20px 20px 20px 100px
  width: 100%

.heading
  text-align: left
  display: flex
  align-items: flex-end
  flex-direction: column
  .heading-row
    display: flex
    align-items: center
    flex-direction: row-reverse
    p
      color: #30DEFF
      font-size: 42px
      font-weight: bold
    .heading-row-line
      width: 400px
      height: 1px
      flex-grow: 1
      background-color: #30DEFF
      margin-right: 20px
  .heading-column
    display: flex
    align-items: flex-start
    width: 100%
    flex-direction: row-reverse
    flex-grow: 1
    .heading-column-line
      width: 1px
      background-color: #30DEFF
      margin-top: 10px
      margin-right: 20px
      height: 400px

/* width */
::-webkit-scrollbar
  width: 5px

/* Track */
::-webkit-scrollbar-track
  background: #c7e4e9
  border-radius: 20px
/* Handle */
::-webkit-scrollbar-thumb
  background: #1f5a65
  border-radius: 20px
</style>
